<template>
  <div class="discover">
    <div class="search-box" v-show="!pdfShow">
      <div class="search-input">
        <div
          class="back"
          v-show="isBack"
          @click="getData(),isBack = false,inputVal='',searchResultList=[],liIsOpen=false,liIsOpenGjz=false"
        ></div>
        <div class="input">
          <input type="text" v-model="inputVal" @input="onSearchKey" placeholder="请输入文章标题进行搜索" />
          <img class="search_img3" src="../../assets/images/icon_search@2x.png" alt />
          <button
            style="width:.5rem;height:.3rem;text-align:center;line-height:.3rem;border-radius:.15rem;background:#45A4B1;color:#fff"
          >搜索</button>
        </div>
      </div>
      <div class="search-list" v-show="inputVal.length > 0">
        <div class="loading" v-show="isLoadingShow">
          <img class="loading_img" src="../../assets/images/loading.gif" alt />
          <p>搜索中...</p>
        </div>
        <div class="empty" v-show="isEmptyShow && searchResultList.length === 0">搜索结果为空</div>
        <ul class="ul_one" v-show="!isLoadingShow">
          <li
            @click="onSearchDetail(item)"
            v-for="(item, index) in searchResultList"
            :key="index"
            v-show="liIsOpen || index < max"
          >
            <p v-html="item.textname1"></p>
            <p v-show="item.keyname!=null">关键字</p>
          </li>
          <li
            v-show="searchResultListGjz.length+searchResultList.length>max"
            style="justify-content:center;"
          >
            <span
              style="width:20%;height:100%;line-height:0.4rem;text-align:center"
              @click="liIsOpen = !liIsOpen"
            >{{liIsOpen && searchResultListGjz.length+searchResultList.length > max?'收起':'更多'}}</span>
          </li>
        </ul>
      </div>
    </div>
    <van-tabs
      v-show="!pdfShow"
      v-model="active"
      swipe-threshold="6"
      sticky
      line-width="60"
      @click="getData()"
    >
      <van-tab :title="item.aclass" v-for="(item,index) of findArr" :key="index">
        <div class="content" v-for="(item,index) of item.list" :key="index">
          <div class="title">{{item.textname.split(".")[0]}}</div>
          <div class="promulgator">
            <p>{{item.authorname}}</p>
            <p
              v-if="new Date(item.starttime).toLocaleDateString()==new Date().toLocaleDateString()?true:false"
            >{{new Date(item.starttime).getHours().toString().padStart(2,0)}}:{{new Date(item.starttime).getMinutes().toString().padStart(2,0)}}</p>
            <p
              v-if="new Date(item.starttime).toLocaleDateString()==new Date().toLocaleDateString()?false:true"
            >{{new Date(item.starttime).getFullYear().toString().padStart(2,0)}}/{{(new Date(item.starttime).getMonth()+1).toString().padStart(2,0)}}/{{new Date(item.starttime).getDate().toString().padStart(2,0)}}</p>
          </div>
          <div class="content_con" @click="onPdf(item)">
            <p>{{item.brief}}</p>
            <img :src="item.imagename" alt />
          </div>
          <div class="discuss">
            <div class="left">
              <div @click="onLike($event,item)"></div>
              <p>点赞</p>
              <p>{{item.dianzanliang}}</p>
              <span v-show="likeFlag"></span>
            </div>
            <div class="right">
              <div>
                <img
                  @click="onCollect(item)"
                  :src="item.shoucang===false?require('../../assets/images/essay_btn_collect_gray@2x.png'):require('../../assets/images/btn_collect_selected@2x.png')"
                  alt
                />
              </div>
              <p>收藏</p>
              <p>{{item.shoucangliang}}</p>
            </div>
          </div>
        </div>
      </van-tab>
    </van-tabs>
    <div v-show="pdfShow" class="pdf_box">
      <div class="title">
        <div class="back" @click="pdfShow=false,pdfSrc=''"></div>
        {{pdfTitle}}
      </div>
      <div class="iframe_con">
        <iframe :src="pdfSrc" frameborder="0" scrolling="auto" class="iframe"></iframe>
      </div>
    </div>
    <tabbar v-show="inputVal.length == 0"></tabbar>
  </div>
</template>
<script>
export default {
  name: "Information",
  components: {
    Tabbar: () => import("../../components/Tabbar.vue")
  },
  data() {
    return {
      max: 2,
      liIsOpen: false,
      liIsOpenGjz: false,
      inputVal: "",
      isLoadingShow: false,
      isEmptyShow: false,
      searchResultList: [],
      searchResultListGjz: [],
      isBack: false,
      active: 0,
      likeFlag: false,
      findArr: [],
      userInfoObj: this.$store.state.userInfoObj || {},
      pdfShow: false,
      pdfSrc: "",
      pdfTitle: ""
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.browseRecord(); //浏览记录
      this.getData();
    });
  },

  methods: {
    //点击显示pdf
    onPdf(item) {
      // this.pdfShow =- true
      // this.pdfTitle = item.textname
      // this.pdfSrc = "http://storage.xuetangx.com/public_assets/xuetangx/PDF/PlayerAPI_v1.0.6.pdf"
      window.location.href = item.textpath
    },
    //浏览记录
    browseRecord() {
      this.$axios.post("/hopeUserLog_h/insertFourCh", {
        // aamid:"010203",
        aamid: this.userInfoObj.aamid,
        moduleid: -3,
        logtime: new Date()
      });
    },
    //获取页面视图数据
    getData() {
      this.inputVal = "";
      this.inputVal == "" ? (this.isBack = false) : (this.isBack = true);
      this.$axios
        .post("/hopeActivity/queryHopeActivity", {
          "aamid": this.userInfoObj.aamid,
          "deptid": this.userInfoObj.deptid,
          "odeptid": this.userInfoObj.odeptid,
          "textclass": this.active
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000"){
            this.findArr = [...res.data.data] || [];
          }
        });
    },
    //搜索（值）
    onSearchKey() {
      this.inputVal = this.inputVal.trim();
      this.inputVal == "" ? (this.isBack = false) : (this.isBack = true);
      if (this.inputVal) {
        this.isLoadingShow = true;
        this.isEmptyShow = false;
        const reg = new RegExp(this.inputVal, "g");
        this.$axios
          .post("/hopeModule/findPageSearch", {
            name: this.inputVal,
            aamid: this.userInfoObj.aamid,
            deptid: this.userInfoObj.deptid,
            odeptid: this.userInfoObj.odeptid,
            textclass: this.active
          })
          .then(res => {
            this.isLoadingShow = false;
            if (res.status === 200 && res.data.status === "000000") {
              const resData = res.data.data.list[0].styleList || [];
              const resData1 = res.data.data.list[1].styleList || [];
              if (resData.length === 0 && resData1.length === 0)
                this.isEmptyShow = true;
              // 关键字高亮
              resData.forEach(i => {
                i.textname1 = i.textname;
                i.textname1 = i.textname1.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                );
              });
              resData1.forEach(i => {
                i.textname1 = i.keyname;
                i.textname1 = i.textname1.replace(
                  reg,
                  "<span style='color: #45A4B1;'>" + this.inputVal + "</span>"
                );
                resData.push(i);
              });
              this.searchResultList = resData;
              this.searchResultListGjz = resData1;
            } else {
              this.isEmptyShow = true;
            }
          })
          .catch(err => {
            this.isLoadingShow = false;
            this.isEmptyShow = true;
          });
      }
    },
    //点击搜索详情
    onSearchDetail(item) {
      if (item.keyname === null) {
         window.location.href = item.textpath
      } else {
        this.onSearchBtn(this.searchResultListGjz);
      }
    },
    //搜索（按钮）
    onSearchBtn(arr) {
      if (this.inputVal) {
        this.findArr[this.active].list = arr;
        this.inputVal = "";
        this.liIsOpen = false;
        this.liIsOpenGjz = false;
      }
    },
    //点赞
    onLike(e, item) {
      this.likeFlag = true;
      e.target.className = this.likeFlag ? "like_animate" : "";
      var timer = setTimeout(() => {
        clearTimeout(timer);
        this.likeFlag = false;
        e.target.className = this.likeFlag ? "like_animate" : "";
      }, 600);
      item.dianzanliang++
      this.$axios
        .post("/hopeActivity/commendhopeActivity", {
          aamid: this.userInfoObj.aamid,
          activityid: item.activityid,
          viewtime: new Date(),
          liked: 1
        })
    },
    //收藏
    onCollect(item) {
      item.shoucang = !item.shoucang;
      if (item.shoucang === true) {
        item.shoucangliang++
        this.$toast({
          message: "已收藏",
          icon: require("../../assets/images/btn_collect_selected@2x.png")
        });
        this.$axios
          .post("/hopeActivity/colletcthopeActivity", {
            aamid: this.userInfoObj.aamid,
            activityid: item.activityid,
            activitysequence: new Date(),
            favortype: 1
          })
      } else {
        item.shoucangliang--
        this.$toast({
          message: "取消收藏",
          icon: require("../../assets/images/btn_collect_normal@2x.png")
        });
        this.$axios
          .post("/hopeActivity/deleteCollActivi", {
            aamid: this.userInfoObj.aamid,
            activityid: item.activityid
          })
      }
    }
  }
};
</script>
<style lang="less" scoped>
