        font-size: 0.14rem;
        color: #666666;
        .loading_img {
          width: 0.4rem;
        }
      }
      .empty {
        padding: 1.2rem 0;
        text-align: center;
        font-size: 0.14rem;
        color: #666666;
      }
      .ul_one {
        padding-bottom: 1rem;
        li {
          height: 0.4rem;
          color: #232323;
          border-bottom: 1px solid #eaeaea;
          box-sizing: border-box;
          padding: 0 0.17rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          p:first-of-type {
            width: 80%;
            height: 100%;
            line-height: 0.4rem;
            font-size: 0.14rem;
            overflow: hidden;
            -ms-text-overflow: ellipsis;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          p:last-of-type {
            width: 0.5rem;
            height: 0.18rem;
            border-radius: 0.08rem;
