            line-height: 0.18rem;
            color: #45a4b1;
            font-size: 0.12rem;
          }
        }
      }
    }
  }
  /deep/ .van-tabs__wrap {
    background: #fff;
    margin-bottom: 0.5rem;
    z-index: 100;
    .van-tabs__line {
      background-color: #45a4b1;
    }
    .van-tab--active {
      font-weight: bold;
    }
  }
  /deep/ .van-tab__pane {
    overflow: hidden;
  }
  .content {
    width: 95%;
    height: 1.85rem;
    margin: auto;
    background: #fff;
    border-radius: 0.1rem;
    margin-top: 0.1rem;
    padding: 0.15rem 0.1rem 0 0.1rem;
    box-sizing: border-box;
    .title {
      width: 100%;
      height: 0.3rem;
      color: #232323;
      font-size: 0.18rem;
      font-weight: bold;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .promulgator {
      width: 100%;
      height: 0.28rem;
      font-size: 0.16rem;
      color: #969696;
      p:first-of-type {
        float: left;
      }
      p:last-of-type {
        float: left;
        margin-left: 0.1rem;
      }
    }
    .content_con {
      width: 100%;
      height: 0.66rem;
      margin-bottom: 0.08rem;
      display: flex;
      justify-content: space-between;
      p {
        width: 2.3rem;
        height: 100%;
        color: #3a3a3a;
        font-size: 0.15rem;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3; //在这里控制指定行数超出换行哦
        -webkit-box-orient: vertical;
      }
      img {
        width: 0.95rem;
        height: 0.64rem;
      }
    }
    .discuss {
      width: 100%;
      height: 0.2rem;
      display: flex;
      .left {
        height: 100%;
        position: relative;
        span {
          width: 100%;
          height: 100%;
          display: block;
          position: absolute;
          left: 0;
          top: 0;
        }
        div {
          width: 0.2rem;
          height: 0.2rem;
          background: url("../../assets/images/essay_btn_good_gray@2x.png")
            no-repeat center center;
          background-size: 0.2rem 0.2rem;
          float: left;
          margin-right: 0.05rem;
        }
        .like_animate {
          animation: like_animate 0.6s linear forwards;
        }
        @keyframes like_animate {
          0% {
            transform: rotate(0deg);
            background: url("../../assets/images/btn_good_selected@2x.png")
              no-repeat center center;
            background-size: 0.2rem 0.2rem;
          }
          25% {
            transform: rotate(-30deg);
          }
          50% {
            transform: rotate(0deg);
          }
          70% {
            transform: rotate(30deg);
          }
          100% {
            transform: rotate(0deg);
            background: url("../../assets/images/essay_btn_good_gray@2x.png")
              no-repeat center center;
            background-size: 0.2rem 0.2rem;
          }
        }
        p:first-of-type {
          width: 0.3rem;
          height: 100%;
          text-align: center;
          line-height: 0.2rem;
          color: #a5a5a5;
          font-size: 0.12rem;
          margin-right: 0.05rem;
          float: left;
        }
        p:last-of-type {
          height: 100%;
          line-height: 0.2rem;
          color: #a5a5a5;
          font-size: 0.12rem;
          float: left;
        }
      }
      .right {
        height: 100%;
        margin-left: 0.2rem;
        position: relative;
        span {
          width: 100%;
          height: 100%;
          display: block;
          position: absolute;
          left: 0;
          top: 0;
        }
        div {
          width: 0.2rem;
          height: 0.2rem;
          background-size: 0.2rem 0.2rem;
          float: left;
          margin-right: 0.05rem;
          img {
            width: 100%;
            height: 100%;
          }
        }
        p:first-of-type {
          width: 0.3rem;
          height: 100%;
          text-align: center;
          line-height: 0.2rem;
          color: #a5a5a5;
          font-size: 0.12rem;
          margin-right: 0.05rem;
          float: left;
        }
        p:last-of-type {
