          color: #a5a5a5;
          font-size: 0.12rem;
          float: left;
        }
      }
    }
  }
  .pdf_box {
    width: 100%;
    height: 100vh;
    background: #fff;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow: auto;
    .title {
      width: 100%;
      height: 0.5rem;
