      }
    }
    .iframe_con {
      width: 100%;
      height: 100%;
      overflow: hidden;
      .iframe {
        width: 108%;
        height: 110%;
        margin-left: -0.05rem;
        margin-top: -0.6rem;
        margin-bottom: -0.1rem;
      }
    }
  }
}
</style>
